@use 'variables' as v;

@mixin telefono-antigo {
    @media (min-width: #{v.$telefono-antigo}) {
        @content;
    }
}

@mixin telefono {
    @media (min-width: #{v.$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{v.$tablet}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{v.$desktop}) {
        @content;
    }
}