@use 'base/variables' as v;
@use 'base/mixins' as m;

.tituloH1 {
    font-size: 2.5rem;
    font-family: v.$fuenteTitulos;
    text-align: center;
    color: v.$blanco;
    margin: 3rem 0;
    letter-spacing: 0.2rem;

    @include m.tablet {
        font-size: 3rem;
        letter-spacing: 0.5rem;
    }
}

.tituloH2 {
    font-size: 2rem;
    font-family: v.$fuenteTitulos;
    color: v.$blanco;
    font-weight: 700;
    letter-spacing: 0.2rem;

    @include m.tablet{
        font-size: 3rem;
        letter-spacing: 0.5rem;
    }
}

.tituloH3 {
    font-size: 1.5rem;
    font-family: v.$fuenteTitulos;
    text-align: center;
    color: v.$blanco;

    @include m.tablet {
        font-size: 2rem;
    }
}

.verificacionGif {
    width: 75%;
    margin-bottom: 20rem;

    @include m.tablet {
        width: 50%;
    }
}

.final{
    margin-bottom: 30rem;
}

.carta-width{
    width: 90%;
}

.carta-height{
    height: 20rem;
}

#datos_ordenes,
#datos_ordenes_wrapper,
#datos_auditar,
#datos_auditar_wrapper,
#datos_productos,
#datos_productos_wrapper,
#datos_carrito,
#datos_carrito_wrapper,
#datos_pedidos,
#datos_pedidos_wrapper {
    font-size: 1.6rem;
    color: v.$negro;
}

.w-10{
    width: 20% !important;
    @include m.tablet{
        width: 10% !important;
    }
}