@use 'base/variables' as v;
@use 'base/mixins' as m;

.home {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    row-gap: 2rem;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .center {
        text-align: center;
        grid-column: 1 / 3;
        grid-row: 2 / 3;
    }

    .right {
        grid-column: 2 / 3;
        grid-row: 1 / 2;

        @include m.tablet {
            max-width: 7rem;
        }
    }
    padding-right: 1rem;

    .logo {
        font-size: 3rem;
        font-family: v.$fuenteTitulos;
        font-weight: 700;
        color: v.$blanco;
        letter-spacing: 0.2rem;
        text-decoration: none;

        &:hover {
            color: v.$linkHover;
        }

        @include m.tablet {
            font-size: 4rem;
            letter-spacing: 0.5rem;
        }
    }

    .link {
        font-size: 2rem;
        font-family: v.$fuenteTitulos;
        font-weight: 400;
        color: v.$blanco;
        text-decoration: none;

        &:hover {
            color: v.$linkHover;
        }
    }
}

.categorias {
    #offcanvasHome {
        background-color: v.$offcanva;
    }

    .offcanva_menu {
        font-size: 2rem;
        font-family: v.$fuenteParrafos;
        color: v.$blanco;
        text-decoration: none;

        &:hover{
            letter-spacing: 1rem;
            transition-duration: 1s;
            color: v.$linkHover;
        }
    }

}

.carrusel-foto {
    display: block;
    width: 100% !important;
    height: 30rem;

    @include m.tablet{
        height: 60rem;
    }
}
