@use 'base/variables' as v;
@use 'base/mixins' as m;

.menu {
    text-align: center;
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;

    .logo {
        font-size: 3rem;
        font-family: v.$fuenteTitulos;
        font-weight: 700;
        color: v.$blanco;
        letter-spacing: 0.2rem;
        text-align: center;
        text-decoration: none;

        @include m.tablet {
            font-size: 4rem;
            letter-spacing: 0.5rem;
        }

        &:hover {
            color: v.$linkHover;
        }
    }

}

.barra {
    padding-top: 3rem;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .titulo {
        color: v.$blanco;
        letter-spacing: 0.2rem;
        text-align: center;
        margin: 2rem 0;
        font-size: 3rem;
    }
}  

.navegacion{
    .enlace {
        display: block;
        text-align: center;
        font-size: 1.8rem;
        color: v.$blanco;
        text-decoration: none;
        margin: 1rem 0;

        &:hover {
            color: v.$linkHover;
        }
    }
    
    @include m.tablet {
        display: flex;
        gap: 2rem;
        margin-top: 0;
        align-items: center;
    } 
}

.enlace_dropdown {
    font-size: 1.8rem;
    color: v.$negro;
    text-decoration: none;
    padding: 1rem;
    cursor: pointer;
    
    &:hover {
        text-decoration: underline;
        color: v.$negro;
    }
}