@use 'base/variables' as v;
@use 'base/mixins' as m;

.register {
    text-align: center;
    color: v.$negro;

    label {
        font-size: 2rem;
        letter-spacing: 0.2rem;
        font-weight: 700;

        @include m.tablet {
            font-size: 2.2rem;
        }
    }

    input:not([type="submit"], [type="button"]), select {
        font-size: 1.5rem;
        border-radius: 1rem;
        border-style: solid;
        text-align: center;

        @include m.tablet {
            font-size: 2rem;
            border-radius: 1rem;
            border-style: solid;
        }
    }

    .enlace {
        color: v.$blanco;
        font-size: 2rem;
        text-decoration: none;
    }
}