@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    padding-top: 1rem;
    display: flex;
    justify-content: center;

    .titulo {
        font-size: 2rem;
        font-family: v.$fuenteTitulos;
        font-weight: bold;
    }

    .parrafo {
        font-size: 2rem;
        line-height: 1;
    }

    .enlace {
        font-size: 2rem;
        line-height: 1;
        color: v.$negro;

        &:hover {
            color: v.$negro;
        }
    }


    .politica-privacidad {
        font-size: 1.5rem;
        font-style: italic;
        text-align: center;
        padding: 9rem 2rem 2rem 2rem;
        margin-bottom: 0;
    }
}