@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {

    margin-bottom: 30rem;
    
    @include m.tablet{
        margin-bottom: 0;
    }

    legend {
        font-size: 2rem;
        font-weight: 700;
        letter-spacing: 0.2rem;
        color: v.$blanco;
        margin: 2rem 0;

        @include m.tablet {
            font-size: 2.5rem;
        }
    }

    label {
        font-size: 1.8rem;
        font-weight: 700;
        display: block;

        @include m.tablet {
            font-size: 2rem;
        }
    }

    input:not([type="submit"], [type="button"]),
    textarea {
        padding: 0 1rem;
        display: block;
        width: 75%;
        font-size: 1.8rem;
        background-color: v.$blanco;
        border: 1px solid v.$negro;
        border-radius: 1rem;
        margin-bottom: 2rem;

        @include m.tablet {
            font-size: 2rem;
            width: 50%;
        }
    }

    textarea {
        height: 20rem;
        resize: none;
    }
}