@use 'base/variables' as v;
@use 'base/mixins' as m;

.login {
    color: v.$blanco;

    .titulo {
        font-family: v.$fuenteTitulos;
        text-align: center;
        font-size: 2rem;
        font-weight: 700;
        line-height: 1.5;
        letter-spacing: 0.2rem;

        @include m.tablet {
            font-size: 3rem;
            letter-spacing: 0.5rem;
        }
    }

    label {
        font-size: 2rem;
        text-align: center;
        letter-spacing: 0.2rem;
        font-weight: 700;

        @include m.tablet {
            font-size: 2.5rem;
        }
    }

    input:not([type="submit"]) {
        width: 75%;
        text-align: center;
        font-size: 1.5rem;
        border-radius: 1rem;
        border-style: solid;
        text-align: center;

        @include m.tablet {
            width: 30%;
            font-size: 2rem;
            border-radius: 1rem;
            border-style: solid;
        }
    }

    .enlace {
        color: v.$blanco;
        font-size: 2rem;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }
}
