@charset "UTF-8";
/* Colores */
/* Fuentes */
/* Tamaño de Media Queries */
html {
  /* 1rem = 10px */
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #1c8fac;
  font-family: "Open Sans", sans-serif;
  position: relative;
  padding-bottom: 20rem;
  min-height: 120vh;
}
@media (min-width: 300px) {
  body {
    min-height: 100vh;
  }
}
@media (min-width: 480px) {
  body {
    padding-bottom: 15rem;
  }
}

hr {
  width: 75%;
  color: #ffffff;
}
@media (min-width: 768px) {
  hr {
    width: 25%;
  }
}

footer {
  position: absolute;
  bottom: 0;
}

.tituloH1 {
  font-size: 2.5rem;
  font-family: "PT Sans", sans-serif;
  text-align: center;
  color: #ffffff;
  margin: 3rem 0;
  letter-spacing: 0.2rem;
}
@media (min-width: 768px) {
  .tituloH1 {
    font-size: 3rem;
    letter-spacing: 0.5rem;
  }
}

.tituloH2 {
  font-size: 2rem;
  font-family: "PT Sans", sans-serif;
  color: #ffffff;
  font-weight: 700;
  letter-spacing: 0.2rem;
}
@media (min-width: 768px) {
  .tituloH2 {
    font-size: 3rem;
    letter-spacing: 0.5rem;
  }
}

.tituloH3 {
  font-size: 1.5rem;
  font-family: "PT Sans", sans-serif;
  text-align: center;
  color: #ffffff;
}
@media (min-width: 768px) {
  .tituloH3 {
    font-size: 2rem;
  }
}

.verificacionGif {
  width: 75%;
  margin-bottom: 20rem;
}
@media (min-width: 768px) {
  .verificacionGif {
    width: 50%;
  }
}

.final {
  margin-bottom: 30rem;
}

.carta-width {
  width: 90%;
}

.carta-height {
  height: 20rem;
}

#datos_ordenes,
#datos_ordenes_wrapper,
#datos_auditar,
#datos_auditar_wrapper,
#datos_productos,
#datos_productos_wrapper,
#datos_carrito,
#datos_carrito_wrapper,
#datos_pedidos,
#datos_pedidos_wrapper {
  font-size: 1.6rem;
  color: #000000;
}

.w-10 {
  width: 20% !important;
}
@media (min-width: 768px) {
  .w-10 {
    width: 10% !important;
  }
}

.footer {
  padding-top: 1rem;
  display: flex;
  justify-content: center;
}
.footer .titulo {
  font-size: 2rem;
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
}
.footer .parrafo {
  font-size: 2rem;
  line-height: 1;
}
.footer .enlace {
  font-size: 2rem;
  line-height: 1;
  color: #000000;
}
.footer .enlace:hover {
  color: #000000;
}
.footer .politica-privacidad {
  font-size: 1.5rem;
  font-style: italic;
  text-align: center;
  padding: 9rem 2rem 2rem 2rem;
  margin-bottom: 0;
}

.menu {
  text-align: center;
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}
.menu .logo {
  font-size: 3rem;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  color: #ffffff;
  letter-spacing: 0.2rem;
  text-align: center;
  text-decoration: none;
}
@media (min-width: 768px) {
  .menu .logo {
    font-size: 4rem;
    letter-spacing: 0.5rem;
  }
}
.menu .logo:hover {
  color: #F9F871;
}

.barra {
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .titulo {
  color: #ffffff;
  letter-spacing: 0.2rem;
  text-align: center;
  margin: 2rem 0;
  font-size: 3rem;
}

.navegacion .enlace {
  display: block;
  text-align: center;
  font-size: 1.8rem;
  color: #ffffff;
  text-decoration: none;
  margin: 1rem 0;
}
.navegacion .enlace:hover {
  color: #F9F871;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    gap: 2rem;
    margin-top: 0;
    align-items: center;
  }
}

.enlace_dropdown {
  font-size: 1.8rem;
  color: #000000;
  text-decoration: none;
  padding: 1rem;
  cursor: pointer;
}
.enlace_dropdown:hover {
  text-decoration: underline;
  color: #000000;
}

.home {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  row-gap: 2rem;
  padding-right: 1rem;
}
@media (min-width: 768px) {
  .home {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.home .center {
  text-align: center;
  grid-column: 1/3;
  grid-row: 2/3;
}
.home .right {
  grid-column: 2/3;
  grid-row: 1/2;
}
@media (min-width: 768px) {
  .home .right {
    max-width: 7rem;
  }
}
.home .logo {
  font-size: 3rem;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  color: #ffffff;
  letter-spacing: 0.2rem;
  text-decoration: none;
}
.home .logo:hover {
  color: #F9F871;
}
@media (min-width: 768px) {
  .home .logo {
    font-size: 4rem;
    letter-spacing: 0.5rem;
  }
}
.home .link {
  font-size: 2rem;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  color: #ffffff;
  text-decoration: none;
}
.home .link:hover {
  color: #F9F871;
}

.categorias #offcanvasHome {
  background-color: #00AAB4;
}
.categorias .offcanva_menu {
  font-size: 2rem;
  font-family: "Open Sans", sans-serif;
  color: #ffffff;
  text-decoration: none;
}
.categorias .offcanva_menu:hover {
  letter-spacing: 1rem;
  transition-duration: 1s;
  color: #F9F871;
}

.carrusel-foto {
  display: block;
  width: 100% !important;
  height: 30rem;
}
@media (min-width: 768px) {
  .carrusel-foto {
    height: 60rem;
  }
}

.login {
  color: #ffffff;
}
.login .titulo {
  font-family: "PT Sans", sans-serif;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: 0.2rem;
}
@media (min-width: 768px) {
  .login .titulo {
    font-size: 3rem;
    letter-spacing: 0.5rem;
  }
}
.login label {
  font-size: 2rem;
  text-align: center;
  letter-spacing: 0.2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .login label {
    font-size: 2.5rem;
  }
}
.login input:not([type=submit]) {
  width: 75%;
  text-align: center;
  font-size: 1.5rem;
  border-radius: 1rem;
  border-style: solid;
  text-align: center;
}
@media (min-width: 768px) {
  .login input:not([type=submit]) {
    width: 30%;
    font-size: 2rem;
    border-radius: 1rem;
    border-style: solid;
  }
}
.login .enlace {
  color: #ffffff;
  font-size: 2rem;
  text-decoration: none;
}
.login .enlace:hover {
  text-decoration: underline;
}

.formulario .label {
  color: var(--negro);
  font-size: 2rem;
  letter-spacing: 0.2rem;
  font-weight: 700;
  margin: 0;
}
.formulario .input {
  width: 100%;
  font-size: 1.5rem;
  border-radius: 1rem;
  border-style: solid;
  text-align: center;
}

.register {
  text-align: center;
  color: #000000;
}
.register label {
  font-size: 2rem;
  letter-spacing: 0.2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .register label {
    font-size: 2.2rem;
  }
}
.register input:not([type=submit], [type=button]), .register select {
  font-size: 1.5rem;
  border-radius: 1rem;
  border-style: solid;
  text-align: center;
}
@media (min-width: 768px) {
  .register input:not([type=submit], [type=button]), .register select {
    font-size: 2rem;
    border-radius: 1rem;
    border-style: solid;
  }
}
.register .enlace {
  color: #ffffff;
  font-size: 2rem;
  text-decoration: none;
}

.formulario {
  margin-bottom: 30rem;
}
@media (min-width: 768px) {
  .formulario {
    margin-bottom: 0;
  }
}
.formulario legend {
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 0.2rem;
  color: #ffffff;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .formulario legend {
    font-size: 2.5rem;
  }
}
.formulario label {
  font-size: 1.8rem;
  font-weight: 700;
  display: block;
}
@media (min-width: 768px) {
  .formulario label {
    font-size: 2rem;
  }
}
.formulario input:not([type=submit], [type=button]),
.formulario textarea {
  padding: 0 1rem;
  display: block;
  width: 75%;
  font-size: 1.8rem;
  background-color: #ffffff;
  border: 1px solid #000000;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .formulario input:not([type=submit], [type=button]),
.formulario textarea {
    font-size: 2rem;
    width: 50%;
  }
}
.formulario textarea {
  height: 20rem;
  resize: none;
}