@use 'variables' as v;
@use 'base/mixins' as m;

html {
    /* 1rem = 10px */
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: v.$primario;
    font-family: v.$fuenteParrafos;
    position: relative;
    padding-bottom: 20rem;
    min-height: 120vh;

    @include m.telefono-antigo {
        min-height: 100vh;
    }

    @include m.telefono {
        padding-bottom: 15rem;
    }
}


hr {
    width: 75%;
    color: v.$blanco;

    @include m.tablet {
        width: 25%;
    }
}

footer {
    position: absolute;
    bottom: 0;
}